import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Logs } from '@sharedModels';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { LogChart } from '../model/log-chart';

@Injectable({
  providedIn: 'root',
})
export class ViewerService {
  constructor(private http: HttpClient) {}

  // get motor mock
  getMotorDataMock(): Observable<any> {
    return this.http.get<any>('../../../assets/seb.json');
  }

  // get all logs for one tool
  getLogs(id: number): Observable<Logs[]> {
    if (id) {
      return this.http.get<Logs[]>(`${environment.base_url}logs?tool-id=${id}`);
    }
  }

  // get data for specific log
  getLogData(id: number): Observable<LogChart> {
    if (id) {
      return this.http.get<LogChart>(`${environment.base_url}logs/${id}`);
    }
  }
}
