import { Injectable } from '@angular/core';
import * as d3 from 'd3';
import { ChartData, ChartValues } from '../../model/chart-data';
@Injectable({
  providedIn: 'root',
})
export class ChartService {
  // chart margins and width / height setter etc...
  readonly margin = { top: 40, right: 10, bottom: 50, left: 50 };
  private _width = 1000 - this.margin.left - this.margin.right;
  private _height = 560 - this.margin.top - this.margin.bottom;
  get width() {
    return this._width;
  }
  set width(value: number) {
    this._width = value - this.margin.left - this.margin.right;
  }

  get height() {
    return this._height;
  }

  set height(value: number) {
    this._height = value - this.margin.left - this.margin.right;
  }

  // variables used only within this service
  private data: ChartData;
  private svg;
  // x axis vars
  private x;
  private xAxis;
  // y axis vars
  private y;
  private yAxis;
  // brush and lines vars
  private clipPath;
  private brush;
  // tooltip line
  private tooltipLine;
  // tooltip
  private tooltip;
  // tldr; find closest value
  readonly bisectData = d3.bisector((d) => {
    return d.speed;
  }).left;

  private collisionsData = new Array<ChartValues>();

  // draw chart
  draw(data: ChartData): void {
    this.data = { ...data };
    this.drawSVG();
  }

  // create svg for the chart
  private drawSVG(): void {
    this.svg = d3
      .select('#chart')
      .append('svg')
      .attr('id', 'svg')
      .attr('class', 'svg')
      .attr('width', this.width + this.margin.left + this.margin.right)
      .attr('height', this.height + this.margin.top + this.margin.bottom)
      .append('g')
      .attr('transform', `translate(${this.margin.left}, ${this.margin.top})`);
    // create axis
    this.axis();
    // reset zoom on double click
    this.svg.on('dblclick', () => this.resetZoom());
    // create tooltip line
    this.tipLine();
    // select tooltip element
    this.tooltip = d3.select('.tooltip');
  }

  // create tooltip linle
  private tipLine(): void {
    this.tooltipLine = this.svg
      .select('.brush')
      .append('line')
      .attr('class', 'tooltip-line')
      .attr('y2', this.height)
      .style('opacity', 0)
      .style('stroke', 'black')
      .style('stroke-width', 0.3)
      .style('fill', 'none');
  }

  // create x & y axis
  private axis(): void {
    const axisGroup = this.svg.append('g').attr('class', 'axis');
    // x axis settings and draw
    this.x = d3
      .scaleLinear()
      .domain([this.minValue('speed'), this.maxValue('speed') + 50])
      .range([0, this.width]);

    this.xAxis = axisGroup
      .append('g')
      .attr('class', 'x')
      .call(d3.axisBottom(this.x).tickSizeOuter(0).tickSizeInner(this.height));

    // a axis settings and draw
    this.y = d3
      .scaleLinear()
      .domain([this.minValue('torque') - 1000, this.maxValue('torque') + 1000])
      .range([this.height, 0]);
    this.yAxis = axisGroup
      .append('g')
      .attr('class', 'y')
      .call(d3.axisLeft(this.y).tickSizeOuter(0).tickSizeInner(-this.width));
    // calling other methods
    this.brushHandler();
    this.paths();
    this.circles(0);
    this.axisLabels();
    this.styling();
  }

  // create and position labels for axis
  private axisLabels(): void {
    // group labels
    const labels = this.svg.append('g').attr('class', 'labels');
    // x axis label
    labels
      .append('text')
      .attr('class', 'xLabel')
      .attr('text-anchor', 'end')
      .attr('x', this.width)
      .attr('y', this.height + 30)
      .style('font-size', '0.9rem')
      .text('Speed');

    // y axis label
    labels
      .append('text')
      .attr('class', 'yLabel')
      .attr('text-anchor', 'start')
      .attr('x', -25)
      .attr('y', -10)
      .style('font-size', '0.9rem')
      .text('Torque');
  }

  // create clip for brush and creates brush
  private brushHandler(): void {
    // prepare rect for lines and brush
    const clip = this.svg
      .append('defs')
      .append('svg:clipPath')
      .attr('id', 'clip')
      .append('svg:rect')
      .attr('width', this.width + this.margin.left + this.margin.right)
      .attr('height', this.height)
      .attr('x', 0)
      .attr('y', 0);

    // create brush and call methods on end
    this.brush = d3
      .brush()
      .extent([
        [0, 0],
        [this.width + this.margin.left + this.margin.right, this.height],
      ])
      .on('end', (event) => this.brushed(event));
    // bind clip path
    this.clipPath = this.svg.append('g').attr('clip-path', 'url(#clip)');

    // append brush to the rectangle
    this.clipPath.append('g').attr('class', 'brush').call(this.brush);

    // change cursor
    d3.select('.overlay').style('cursor', 'default');
    // call mouse events
    this.mouseEvents();
  }
  // handles mouse event except brush
  private mouseEvents(): void {
    d3.select('.overlay')
      .on('mouseover', (event) => this.mouseover(event))
      .on('mousemove', (event) => this.mousemove(event))
      .on('mouseout', () => this.mouseout());
  }
  // draw paths
  private paths(): void {
    // draw lines for each serie
    this.data.series?.forEach((serie) => {
      this.clipPath
        .append('path')
        .datum(serie.values)
        .attr('class', serie.name)
        .attr('stroke', serie.color)
        .attr('fill', 'none')
        // .attr('fill', serie.color)
        // .attr('fill-opacity', 0.5)
        .attr('stroke-width', 1.5)
        .attr(
          'd',
          d3
            .line()
            .x((d) => this.x(d.speed))
            .y((d) => this.y(d.torque))
        );
    });
  }

  // updates chart and all parts
  update(data: ChartData): void {
    if (this.data) {
      this.data = { ...data };
      const newWidth = this.width + this.margin.left + this.margin.right;
      // remove all points
      d3.selectAll('circle').remove();
      // resize svg
      d3.select('svg').attr('width', newWidth);
      // resize holder for interactions
      d3.select('.overlay').attr('width', newWidth);
      // resize holder for interactions
      d3.select('#clip rect').attr('width', newWidth);

      // call methods to resize rest
      this.resizeAxis();
      this.resizePaths();
      this.resizeXLabel();
      this.repositionCollisionPoints();
      this.circles(0);
    }
  }

  // handle the brush functionality
  private brushed(event): void {
    // selected area of brush
    const selection = event.selection;

    if (selection) {
      // change domain of x axis to selected area
      this.x.domain([this.x.invert(selection[0][0]), this.x.invert(selection[1][0])]);
      this.y.domain([this.y.invert(selection[1][1]), this.y.invert(selection[0][1])]);

      // remove brush selection
      this.clipPath.select('.brush').call(this.brush.move);
      // apply new domain to axis
      this.xAxis
        .transition()
        .duration(1000)
        .call(d3.axisBottom(this.x).tickSizeOuter(0).tickSizeInner(this.height));

      this.yAxis
        .transition()
        .duration(1000)
        .call(d3.axisLeft(this.y).tickSizeOuter(0).tickSizeInner(-this.width));

      // resize lines
      this.resizePaths();

      // d3.selectAll('circle').remove();

      // add points on line(s)
      this.circles(0);

      this.repositionCollisionPoints();

      // re-style ticks
      this.styling();
    }
  }

  // add circles to line(s)
  private circles(duration?: number): void {
    // let ms;
    // duration ? (ms = duration) : (ms = 1000);
    // this.data.series.forEach((serie) => {
    //   this.clipPath
    //     .selectAll('.dot')
    //     .data(serie.values)
    //     .enter()
    //     .append('circle')
    //     // .transition()
    //     // .duration(500)
    //     .attr('r', 2)
    //     .attr('cx', (d) => this.x(d.speed))
    //     .attr('cy', (d) => this.y(d.torque))
    //     .attr('fill', serie.color);
    // });
  }

  // mouse move handler
  private mousemove(event): void {
    // get data by position of cursor and process them
    const mouse = d3.pointer(event);
    const mouseData = this.x.invert(mouse[0]);
    this.tooltipHandler(event, mouseData);
    // move line by cursor, 1px for brush functionality
    const coords = `${event.layerX - this.margin.left - 2}px`;
    this.tooltipLine.attr('x1', coords).attr('x2', coords);
  }

  // handle tooltip text and events
  private tooltipHandler(event, mouseData): void {
    let html = '';
    this.data.series.forEach((serie, i) => {
      const copyx = [...serie.values];
      const sorted = copyx.sort((a, b) => {
        return a.speed - b.speed;
      });
      const index = this.bisectData(sorted, mouseData);
      const selectedData = sorted[index];
      const speed = selectedData?.speed;

      // holder for the values
      const tempArr = [];
      sorted.forEach((v) => v.speed === speed && tempArr.push(v));
      // create text for the div
      html += `<span class="${serie.name} font-semibold" style="color: ${serie.color}">
      speed: <span class="text-white font-thin text-md">
      ${
        i === 1 && speed > serie.values[1].speed
          ? 'Not available'
          : selectedData?.speed
          ? selectedData?.speed.toFixed(2)
          : 'Not available'
      }
      </span><br/>
      torque positive: <span class="text-white font-thin text-md">
      ${
        i === 1 && speed > serie.values[1].speed
          ? 'Not available'
          : tempArr[0]?.torque && tempArr[0]?.torque > 0
          ? tempArr[0]?.torque.toFixed(2)
          : 'Not available'
      }</span><br/>
      <span class="font-semibold" style="color: ${serie.color}">
      torque negative: <span class="text-white font-thin text-md">
      ${
        i === 1 && tempArr[0]?.torque < 0
          ? tempArr[0]?.torque.toFixed(2)
          : tempArr[1]?.torque
          ? tempArr[1].torque.toFixed(2)
          : 'Not available'
      }
      </span>
       </span>
       <br/>
      `;

      // style tooltip
      const w = window;
      this.tooltip.html(html);
      if (event.pageX >= w.innerWidth * 0.9) {
        this.tooltip.style('left', `${event.clientX - 250}px`).style('top', `${event.clientY}px`);
      } else if (event.pageX <= w.innerWidth * 0.9) {
        this.tooltip.style('left', `${event.clientX + 20}px`).style('top', `${event.clientY}px`);
      }
    });
  }

  // mouse out handler
  private mouseout(): void {
    this.tooltipLine.style('opacity', 0);
    this.tooltip.style('opacity', 0);
  }
  // mouse over handler
  private mouseover(event): void {
    this.tooltipLine.style('opacity', 0.7);
    this.tooltip.style('opacity', 1);
  }

  // resize axis
  private resizeAxis(): void {
    // resize x axis
    this.x = d3
      .scaleLinear()
      .domain([this.minValue('speed'), this.maxValue('speed') + 50])
      .range([0, this.width]);
    // resize ticks and animate
    this.svg
      .select('.x')
      .transition()
      .duration(1000)
      .call(d3.axisBottom(this.x).tickSizeOuter(0).tickSizeInner(this.height));

    // resize y axis
    this.y = d3
      .scaleLinear()
      .domain([this.minValue('torque') - 1000, this.maxValue('torque') + 1000])
      .range([this.height, 0]);
    // resize ticks and animate
    this.svg
      .select('.y')
      .transition()
      .duration(1000)
      .call(d3.axisLeft(this.y).tickSizeOuter(0).tickSizeInner(-this.width));

    // style ticks
    this.styling();
  }

  // resize paths
  private resizePaths(): void {
    this.data?.series &&
      this.data.series?.forEach((serie) => {
        d3.select(`.${serie.name}`)
          .datum(serie.values)
          .transition()
          .duration(1000)
          .attr(
            'd',
            d3
              .line()
              .x((d) => this.x(d.speed))
              .y((d) => this.y(d.torque))
          );
      });
  }

  // resize labels
  private resizeXLabel(): void {
    d3.select('.xLabel')
      .transition()
      .duration(1000)
      .attr('x', this.width)
      .attr('y', this.height + 30);
  }

  // reset to the default state
  private resetZoom(): void {
    // d3.selectAll('circle').remove();
    this.x.domain([this.minValue('speed'), this.maxValue('speed') + 50]);
    this.xAxis
      .transition()
      .duration(1000)
      .call(d3.axisBottom(this.x).tickSizeOuter(0).tickSizeInner(this.height));

    this.y.domain([this.minValue('torque') - 1000, this.maxValue('torque') + 1000]);
    this.yAxis
      .transition()
      .duration(1000)
      .call(d3.axisLeft(this.y).tickSizeOuter(0).tickSizeInner(-this.width));

    this.resizePaths();
    this.circles(0);
    this.resizeAxis();
    this.repositionCollisionPoints();
  }

  // style ticks
  private styling(): void {
    // style y ticks
    this.svg.selectAll('.y .tick line').style('stroke', 'grey').style('opacity', '0.15');

    // move path to x axis instead of top
    this.svg.select('.x path').attr('transform', `translate(0,${this.height})`);
    // style x ticks
    this.svg.selectAll('.x .tick line').style('stroke', 'grey').style('opacity', '0.15');
  }

  // find the maximum of property
  private maxValue(property: 'torque' | 'speed'): number {
    let maxValue = 0;
    this.data &&
      this.data?.series?.length &&
      Array.from(this.data.series).forEach((serie) => {
        const max = Math.max.apply(
          Math,
          serie.values.map((o) => {
            return o[property];
          })
        );
        max > maxValue && (maxValue = max);
      });
    return maxValue;
  }

  // find the minimum of property
  private minValue(property: 'torque' | 'speed'): number {
    let minValue = 0;
    this.data &&
      this.data?.series?.length &&
      Array.from(this.data.series).forEach((serie) => {
        const min = Math.min.apply(
          Math,
          serie.values.map((s) => {
            return s[property];
          })
        );
        min < minValue && (minValue = min);
      });
    return minValue;
  }

  collisionPoints(data: ChartValues[]): void {
    setTimeout(() => {
      data &&
        ((this.collisionsData = [...data]),
        this.collisionsData.forEach((d, i) => {
          this.clipPath
            .append('circle')
            .datum(d)
            .attr('class', 'collision')
            .attr('class', `collision-points-${i}`)
            .style('fill', 'red')
            .style('opacity', 0.75)
            .attr('r', 2)
            .attr('cx', (d) => this.x(d.speed))
            .attr('cy', (d) => this.y(d.torque));
        }));
    }, 0);
  }

  private repositionCollisionPoints(): void {
    this.collisionsData.forEach((d, i) => {
      this.clipPath
        .select(`.collision-points-${i}`)
        .datum(d)
        .transition()
        .duration(1000)
        .attr('cx', (d) => this.x(d.speed))
        .attr('cy', (d) => this.y(d.torque));
    });
  }

  remove() {
    d3.select('svg').remove();
  }
}
