import { Component, OnDestroy, OnInit } from '@angular/core';
import { first } from 'rxjs/operators';
import { Logs, PLC, TableColumn, Tool } from '@sharedModels';
import { PlcState } from 'src/app/store/reducers/plc.reducer';
import { Store } from '@ngrx/store';
import { Observable, Subscription } from 'rxjs';
import { AppState } from '@reducers';
import { ToleranceEnum, WebsocketEnums } from '@sharedEnums';
import { ViewerTable } from '../model/table-model';
import { ChartData, ChartSerie, ChartValues } from '../model/chart-data';
import { ViewerService } from '../service/viewer.service';
import { WebsocketService } from 'src/app/shared/websocket/websocket.service';
import { LogService } from '../service/logs.service';
@Component({
  selector: 'app-viewer',
  templateUrl: './viewer.component.html',
})
export class ViewerComponent implements OnInit, OnDestroy {
  // store fun
  store$: Observable<any>;
  storeSubscription: Subscription;
  // list of plc, tools and selected tool
  plcs = new Array<PLC>();
  tools = new Array<Tool>();
  tool: Tool;

  // data of logs
  logs = new Array<ViewerTable>();

  // remove chart
  remove = false;

  // data chart
  data: any;
  // mocked motor curve :)
  motorData = {};
  motorCurve = new Array<ChartValues>();
  // collisions data
  collisions = new Array<ChartValues>();
  // table configuration
  columns: TableColumn[] = [
    {
      name: 'Created On',
      sort_by: false,
      descending: true,
      property_name: 'createdOn',
      cell_type: 'date',
    },
    {
      name: 'PLC Name',
      sort_by: false,
      descending: true,
      property_name: 'plcName',
      cell_type: 'text',
    },
    {
      name: 'Tolerance',
      sort_by: false,
      descending: true,
      property_name: 'tolerance',
      cell_type: 'text',
    },
    {
      name: 'Tolerance Speed',
      sort_by: false,
      descending: true,
      property_name: 'speedTolerance',
      cell_type: 'text',
    },
    {
      name: 'Tolerance Torque',
      sort_by: false,
      descending: true,
      property_name: 'torqueTolerance',
      cell_type: 'text',
    },
    {
      name: 'Collisions',
      sort_by: false,
      descending: true,
      property_name: 'collisions',
      cell_type: 'text',
    },
    {
      name: 'Actions',
      sort_by: false,
      cell_type: 'action',
      actions: [
        {
          type: 'chart',
          icon: 'fa-chart-line',
          text: 'chart',
          title: 'Chart of the log',
        },
        {
          type: 'delete',
          icon: 'fa-trash-alt',
          text: 'remove',
          title: 'Remove the log',
        },
      ],
    },
  ];

  // multi deletation handler
  multiDeleteText = 'Delete traces';
  multiDeleteIDs = new Array<number>();

  // subscription to websocket related to new logs
  wsObs$: Observable<any>;
  wsSub: Subscription;

  constructor(
    private viewer: ViewerService,
    private store: Store<PlcState>,
    private ws: WebsocketService,
    private logsService: LogService
  ) {
    this.store$ = this.store;
    this.wsObs$ = this.ws.sub(WebsocketEnums.NewLog, true);
  }
  ngOnInit(): void {
    // store
    this.storeSubscription = this.store$.subscribe((resp: AppState) => {
      this.plcs = [...resp.plcs.plcs];
      this.tools = [...resp.tools.tools];
      resp.tools.used && this.usedTool(resp.tools.used);
    });

    // logs via ws
    this.wsSub = this.wsObs$.subscribe((msg) => {
      const message: Logs = JSON.parse(msg);
      message.toolInformation.toolId === this.tool?.id && this.updateLogs(message);
    });
  }

  private usedTool(data: { id: number; toolNumber: number }): void {
    this.tools = [
      ...this.tools.map((x) => {
        if (x.plcId === data?.id && x.toolNumber === data?.toolNumber) {
          return { ...x, isActive: true };
        } else {
          return { ...x, isActive: false };
        }
      }),
    ];
  }

  private updateLogs(msg: Logs): void {
    const newLog = new ViewerTable(
      msg.createdOn,
      msg.plcInformation.name,
      Object.entries(ToleranceEnum).find(
        (tolerance) => tolerance[0] === msg.toolInformation.tolerance.type
      )[1],
      msg.toolInformation.tolerance.speedTolerance,
      msg.toolInformation.tolerance.torqueTolerance,
      msg.numberOfCollisions,
      null,
      msg.id,
      msg.toolInformation.toolId,
      false
    );
    let copy = [...this.logs, newLog];
    copy = [
      ...copy.sort((a, b) => new Date(b.createdOn).getTime() - new Date(a.createdOn).getTime()),
    ];
    this.logs = [...copy];
  }

  selectedTool(tool: Tool): void {
    this.tool = { ...tool };
    this.data = undefined;
    this.remove = !this.remove;
    this.viewer
      .getLogs(tool.id)
      .pipe(first())
      .subscribe((resp: Logs[]) => {
        this.mapTableData(resp);
      });
  }

  ngOnDestroy() {
    this.storeSubscription && this.storeSubscription.unsubscribe();
    this.wsSub && this.wsSub.unsubscribe();
  }

  action(item: { type; item }) {
    switch (item.type) {
      case 'chart': {
        this.viewer
          .getLogData(item.item.id)
          .pipe(first())
          .subscribe((resp) => {
            resp?.collisionPoints?.length && (this.collisions = resp.collisionPoints);
            const data = new ChartData([
              new ChartSerie('measured', '#3FB6B6', resp.measuredCurve.points),
              new ChartSerie('reference', '#FFC800', resp.referenceCurve.points),
              new ChartSerie('motor', '#CC0000', resp.motorCurve.points),
            ]);
            this.data = { ...data };
          });
        break;
      }
      case 'delete': {
        // console.log(
        //   '%c%s',
        //   'color: black; background: yellowgreen; font-size: 14px;',
        //   'delete this fancy trace :)',
        //   item.item.id
        // );
        this.logsService.deletLogs([item.item.id]).subscribe(
          (resp) => console.log('delete successfuly'),
          (err) => console.error(err),
          () => (this.logs = [...this.logs.filter((x) => x.id !== item.item.id)])
        );
        break;
      }
      case 'checkbox': {
        if (this.multiDeleteIDs.length) {
          this.multiDeleteIDs.includes(item.item.id)
            ? this.multiDeleteIDs.splice(this.multiDeleteIDs.indexOf(item.item.id), 1)
            : this.multiDeleteIDs.push(item.item.id);
        } else {
          this.multiDeleteIDs.push(item.item.id);
        }
        break;
      }
      default: {
        break;
      }
    }
  }

  // map data & format them into model for table
  private mapTableData(data: Logs[]): void {
    this.logs = [
      ...data.map(
        (t) =>
          new ViewerTable(
            t.createdOn,
            t.plcInformation.name,
            Object.entries(ToleranceEnum).find(
              (tolerance) => tolerance[0] === t.toolInformation.tolerance.type
            )[1],
            t.toolInformation.tolerance.speedTolerance,
            t.toolInformation.tolerance.torqueTolerance,
            t.numberOfCollisions,
            null,
            t.id,
            t.toolInformation.toolId,
            false
          )
      ),
    ];
    this.logs.sort((a, b) => new Date(b.createdOn).getTime() - new Date(a.createdOn).getTime());
  }

  multiRemoval(cancel?: boolean): void {
    const actions = this.columns[this.columns.length - 1].actions;
    if (!cancel) {
      switch (this.multiDeleteText) {
        case 'Delete selected': {
          this.multiDeleteText = 'Delete traces';
          this.columns[this.columns.length - 1].actions.splice(actions.length - 1, 1);
          this.columns[this.columns.length - 1].actions.push({
            type: 'delete',
            icon: 'fa-trash-alt',
            text: 'remove',
            title: 'Remove the log',
          });
          this.logsService.deletLogs(this.multiDeleteIDs).subscribe(
            (resp) => console.log('delete successfuly'),
            (err) => console.error(err),
            () => {
              this.multiDeleteIDs.forEach(
                (id) => (this.logs = [...this.logs.filter((x) => x.id !== id)])
              );
              this.multiDeleteIDs = [];
            }
          );
          break;
        }

        case 'Delete traces': {
          this.multiDeleteText = 'Delete selected';
          actions.push({
            type: 'checkbox',
            icon: null,
            text: null,
            title: 'Remove the log',
          });
          actions.splice(
            actions.findIndex((x) => x.type === 'delete'),
            1
          );
          this.multiDeleteIDs = [];
          break;
        }
      }
    } else {
      cancel &&
        (this.columns[this.columns.length - 1].actions.splice(actions.length - 1, 1),
        (this.multiDeleteIDs = []),
        this.columns[this.columns.length - 1].actions.push({
          type: 'delete',
          icon: 'fa-trash-alt',
          text: 'remove',
          title: 'Remove the log',
        }),
        (this.multiDeleteText = 'Delete traces'));
    }
  }
}
