import { ChangeDetectionStrategy, Component, Input, NgZone, OnInit } from '@angular/core';
import { ResizeObserver } from 'resize-observer';
import { ChartData, ChartValues } from '../../model/chart-data';
import { ChartService } from './chart.service';

@Component({
  selector: 'app-chart',
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: ` <div id="holder" class="w-full">
    <div class="flex flex-row text-xs ml-12 mt-2 -mb-2" *ngIf="data">
      <div class="flex flex-row">
        <div style="background-color:'#CC0000'; width: 20px; height: 5px;" class="my-auto"></div>
        <span class="ml-1"> motor curve</span>
      </div>
      <div class="flex flex-row mx-5">
        <div style="background-color:'#FFC800'; width: 20px; height: 5px;" class="my-auto"></div>
        <span class="ml-1"> reference curve</span>
      </div>
      <div class="flex flex-row">
        <div style="background-color:'#3FB6B6'; width: 20px; height: 5px;" class="my-auto"></div>
        <span class="ml-1">measured curve</span>
      </div>
    </div>
    <div id="chart" class="chart w-full h-full transition-all duration-1000"></div>
    <div class="" *ngIf="!data" style="height: 560px">
      <app-no-data [title]="'Select a log to display chart'"></app-no-data>
    </div>
    <div
      class="tooltip opacity-0 fixed border-black  bg-black bg-opacity-75 border p-2 rounded text-sm"
    ></div>
  </div>`,
})
export class ChartComponent implements OnInit {
  data: ChartData;
  xData = new Array<number>();

  testData: any;

  observer;

  @Input() set chartData(data: ChartData) {
    data ? ((this.data = { ...data }), this.handleData()) : (this.data = null);
  }
  @Input() set remove(remove: boolean) {
    this.chart.remove();
  }

  @Input() set collisions(data: ChartValues[]) {
    data && data.length && this.chart.collisionPoints(data);
  }
  constructor(private chart: ChartService, private zone: NgZone) {}

  ngOnInit(): void {
    this.observer = new ResizeObserver((entries) => {
      this.zone.run(() => {
        this.chart.width = entries[0].contentRect.width - 4;
        this.chart.update(this.data);
      });
    });
    this.observer.observe(document.getElementById('chart'));
  }

  // parse data and sends them to chart instance
  private handleData(): void {
    const svg = document.getElementById('svg');
    !svg && this.data
      ? ((this.chart.width = document.getElementById('chart').offsetWidth),
        this.chart.draw(this.data))
      : this.chart.update(this.data);
  }
}
