import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Logs } from '@sharedModels';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root',
})
export class LogService {
  constructor(private http: HttpClient) {}
  deletLogs(ids: number[]): Observable<any> {
    const options = { headers: new HttpHeaders({ 'Content-type': 'application/json' }), body: ids };
    return this.http.delete<Logs[]>(`${environment.base_url}logs`, options);
  }
}
